@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
.nav{display:flex;justify-content:space-between;padding:1rem 2rem;width:100%;background-color:var(--color-bg-1)}.nav__left{width:100%}@media only screen and (max-width: 600px){.nav__left{width:60%}}.nav__right{width:50%;display:flex;justify-content:flex-end}.nav__right .nav__item{color:var(--color-white);margin:0 1rem}@media only screen and (max-width: 900px){.nav__right{width:100%}}.nav__item{font-size:1.1rem;letter-spacing:0.1rem;color:var(--color-span)}.nav__item:hover{color:var(--color-span)}

footer{background-color:var(--color-bg-1);padding:1rem;color:var(--color-white)}footer span{display:flex;justify-content:center}footer a{color:var(--color-span)}

.pagination{display:flex;justify-content:space-between}.pagination>button{padding:1rem;border-radius:5px;background:#fff;border:none}.pagination>button:hover{background-color:#ccc}.pagination>button:active{transform:translateY(3px)}

.search{width:50%;margin:1rem auto}.search .field{display:flex;flex-direction:column}.search .field>label{margin-bottom:0.5rem;font-size:1rem}.search .field>input{padding:0.3rem;border-radius:3px;border:none}

.error{padding:1rem;border:3px solid #d93025;border-radius:7px;background-color:tomato}

.box{border-radius:10px;display:flex;background-color:var(--color-bg-4);overflow:hidden}.box__image{width:170px;height:170px}.box .description{padding:0.5rem 1rem;font-family:var(--font-text);font-size:1.1rem;width:100%;display:flex;justify-content:space-between;position:relative;align-items:center}.box .description__left{width:40%;color:#fff}.box .description__right{width:60%}.box .description .more-info{position:absolute;right:0px;width:70px;color:#fff;text-decoration:underline;align-self:start}.box .description p:not(:last-child){margin-bottom:0.8rem}.box .description .name{color:#fff !important}.box .description .name:hover{color:var(--color-span) !important}

.charsPage{width:90%;margin:0 auto}.error{position:absolute;top:20px;left:5px}.grid-container{margin:2rem 4rem}.grid{display:grid;grid-gap:1rem;grid-template-columns:repeat(auto-fit, minmax(400px, 1fr));margin:2rem auto}

:root{--color-bg-1: #343642;--color-bg-2: #44475a;--color-bg-3: #767a8d;--color-bg-4: #4d5475;--color-span: #faea83;--color-white: #fff;--color-black: #000;--color-grey: #aaa999;--font-text: 'Open Sans Condensed', sans-serif;--font-title: 'Lato', sans-serif}*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}body{box-sizing:inherit;font-family:'Lato', sans-serif;font-family:var(--font-title);background-color:#44475a;background-color:var(--color-bg-2);color:#fff;color:var(--color-white);font-size:62.5}button:focus,input:focus{outline:none}ul{list-style:none}a{text-decoration:none;color:#faea83;color:var(--color-span)}

